export default {
	data() {
		return {
			exchangeInfo:[],
		}
	},
	onShow() {
		this.getExchangeInfo();
	},
	onLoad() {
		this.isLogin(); 
	},
	methods: {
		getExchangeInfo() {
			let that = this;
			that.baseGet(
				that.U({
					c: 'store_api',
					a: 'exchange_tree_info'
				}),
				function(res) {
					console.log(res);
					that.exchangeInfo=res.data;
				},
				function(res) {
					console.log(res);
				},
				true
			);
		},
		navToExchangeGoods(index){
			uni.navigateTo({
				url:'/pages/subscribe/exchange_goods/exchange_goods?index='+index
			})
		}
	}
}
